.index-wrap {
    width: 100%;
    height: 100%;
    display: flex;
    .left-bar {
        flex-basis: 60px;
        border-right: 1px solid;
        .nav {
            height: 60px;
            line-height: 60px;
            border-bottom: 1px solid;
            cursor: pointer;
        }
    }
    .main {
        flex-grow: 1;
    }
}